import React, { useEffect, useState, Fragment, useContext } from "react";
import { Select } from "semantic-ui-react";

import { withRouter } from "react-router-dom";
import axios from "axios";
import { makeStyles } from "@material-ui/core/styles";
import Modal from "@material-ui/core/Modal";
import Backdrop from "@material-ui/core/Backdrop";
import Fade from "@material-ui/core/Fade";
import UserContext from "./../UserContext";
import { useHistory } from "react-router-dom";

const useStyles = makeStyles(theme => ({
  modal: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center"
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    border: "2px solid #000",
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3)
  }
}));

const Companies = () => {
  let history = useHistory();
  const { setConfig, notifySuccess } = useContext(UserContext);
  // const userStore = useContext(UserContext);
  const [companies, setCompanies] = useState();
  // const [employeeRole,setRole]=useState();
  const [open, setOpen] = useState(false);
  const [selectedCompany, setSelectedCompany] = useState(false);
  const classes = useStyles();

  const handleOpen = selectedCompany => {
    console.log(selectedCompany);
    setSelectedCompany(selectedCompany);
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  useEffect(() => {
    axios
      .get(
        "https://testapp20200313053149.azurewebsites.net/api/company",
        setConfig
      )
      .then(data => {
        setCompanies(data.data);
        console.log(data);
      });
  }, []);

  const editRole = (role, employee) => {
    var userValue = {
      role: role,
      email: employee.email
    };
    axios
      .post(
        "https://testapp20200313053149.azurewebsites.net/api/user/editEmployeeRole",
        userValue,
        setConfig
      )
      .then(response => {
        console.log(response);
      })
      .catch(e => {
        console.log(e);
      });
  };
  const removeEmployee = companyToDelete => {
    console.log(companyToDelete.id);
    axios
      .delete(
        `https://testapp20200313053149.azurewebsites.net/api/company/${companyToDelete.id}`,
        companyToDelete.id,
        setConfig
      )
      .then(response => {
        notifySuccess("Employee was deleted");
        axios
          .get(
            "https://testapp20200313053149.azurewebsites.net/api/company",
            setConfig
          )
          .then(data => {
            setCompanies(data.data);
            console.log(data);
          });
        handleClose();
      })
      .catch(e => {
        console.log(e);
      });
  };

  return (
    <Fragment>
      <div
        style={{
          display: "flex",
          WebkitJustifyContent: "space-around",
          paddingRight: "50%",
          alignItems: "baseline"
        }}
        className="item"
      >
        <h1 style={{ margin: 0 }}>Companies</h1>

        <h1 style={{ margin: 0 }}>Employees</h1>
      </div>
      <div className="ui celled list">
        <Fragment>
          {companies
            ? companies.map(company => (
                <div
                  className="item"
                  style={{
                    display: "flex",
                    WebkitJustifyContent: "space-around"
                  }}
                  key={company.id}
                >
                  <div className="content">
                    <div className="header">{company.CompanyName}</div>
                    {company.CompanySector}
                  </div>
                  <select class="ui dropdown" onChange={(companyUser)=>history.push(`/user/${companyUser.currentTarget.value}`)}>
                      <option value=""></option>
                    {company.AppUsers.map(companyUser => (
                      <option className="item" data-value={company.CompanyName}
                      value={companyUser.Id}>
                        {companyUser.FirstName} {companyUser.LastName}
                      </option>
                    ))}
                  </select>

                  <button
                    class="negative ui button"
                    style={{ order: 2 }}
                    onClick={() => handleOpen(company)}
                  >
                    Delete Company
                  </button>
                </div>
              ))
            : "No Employees registered"}
        </Fragment>
      </div>
      <div>
        <Modal
          aria-labelledby="transition-modal-title"
          aria-describedby="transition-modal-description"
          className={classes.modal}
          open={open}
          onClose={handleClose}
          closeAfterTransition
          BackdropComponent={Backdrop}
          BackdropProps={{
            timeout: 500
          }}
        >
          <Fade in={open}>
            <div className={classes.paper}>
              <h1>Are you sure you want to delete this employee?</h1>
              <h2 id="transition-modal-title">CompanyName: </h2>
              <hp>{selectedCompany.CompanyName}</hp>

              <h2 id="transition-modal-title">Email: </h2>
              <p id="transition-modal-description">
                {" "}
                {selectedCompany.CompanySector}
              </p>
              <h2 id="transition-modal-title">Role: </h2>
              <p id="transition-modal-description">{selectedCompany.Address}</p>
              <div
                class="ui red basic cancel  button"
                onClick={() => handleClose()}
              >
                <i class="remove icon"></i>
                No
              </div>
              <div
                class="ui green ok  button"
                onClick={() => removeEmployee(selectedCompany)}
              >
                <i class="checkmark icon"></i>
                Yes
              </div>
            </div>
          </Fade>
        </Modal>
      </div>
    </Fragment>
  );
};
export default withRouter(Companies);
